.NavbarItems {
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);

    width: 95%;
    height: 80px;
    border-radius: 13px;

    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    z-index: 999;
}

.navbar-logo {
    color: #222;
    font-size: 2rem;
    cursor: pointer;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(6, auto);
    gap: 10px;
    list-style: none;
    align-items: center;
}

.nav-links {
    text-decoration: none;
    color: #222;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}

.nav-links i {
    padding-right: 10px;
}

.nav-links:hover {
    background-color: #01959a;
    color: #fff;
    border-radius: 4px;
    transition: all 0.4s ease-in-out;
}

.nav-links-mobile {
    display: none;
}



.menu-icons {
    display: none;
}

@media screen and (max-width: 850px) {

    .NavbarItems {
        z-index: 99;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background: #fff;
        border-radius: 13px;
        width: 100%;
        height: auto;

        position: absolute;
        top: 0;
        left: -100%;
        opacity: 0;
        align-items: stretch;
        text-align: center;
        padding: 80px 0 30px 0;
        margin: 0;

        transition: 0.5s ease;

    }
    
    .nav-menu.active {
        left: 0;
        opacity: 1;
        z-index: -1;
        transition: 0.5s ease;

    }

    .nav-links {
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem 0;
    }

    .nav-links:hover {
        background-color: #01959a;
        transition: none;
    }

    .signup-btn {
        display: none;
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: 0 4rem;
        border-radius: 4px;
        background: #01959a;
        text-decoration: none;
        color: #fff;
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 2px;
    }

    .menu-icons {
        display: inline-block;
        cursor: pointer;
    }

    .menu-icons i {
        font-size: 1.2rem;
        color: #222;
    }
}